const newsBtn = document.querySelectorAll('.news-underlist__btn');



Array.prototype.forEach.call(newsBtn, (elem) => {
  elem.addEventListener('click', () => {
    let perentItem = elem.parentElement;

    
  
    perentItem.classList.toggle('active');
  })
}) 


